import React from 'react'
import { Grid, Grow, Typography } from '@material-ui/core'

import useStyles from './styles.js'
import { NewsCard } from '../NewsCard/NewsCard'

const infoCards = [
    { color: '#fada98', title: 'Latest News', text: 'Give me the latest news' },
    { color: '#fbca41', title: 'News by Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
    { color: '#fbca01', title: 'News by Terms', info: 'Bitcoin, PlayStation 5, Smartphones,india, Donald Trump...', text: 'What\'s up with PlayStation 5' },
    { color: '#fbca61', title: 'News by Sources', info: 'CNN, NDTV, BBC News, Time, IGN, Buzzfeed, ABC News...', text: 'Give me the news from CNN' },
]

export const NewsCards = ({ articles, activeArticle }) => {
    const classes = useStyles()

    if(!articles.length) {
        return (
            <Grow in>
                <Grid className={classes.container} container alignItems='stretch' spacing={3}>
                    {infoCards.map((infoCard) => (
                        <Grid item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}>
                            <div className={classes.card} style={{ backgroundColor: infoCard.color ,color:"brown"}}>
                                <Typography variant="h5">{infoCard.title}</Typography>
                                {
                                    infoCard.info 
                                    ? (<Typography variant="h6">
                                    <strong>
                                        {infoCard.title.split(' ')[2]}
                                        <br />
                                        {infoCard.info}
                                    </strong>
                                    </Typography>) : null }
                                    <Typography variant="h6">Try saying: <br /> <i>{infoCard.text}</i></Typography>
                            </div>
                        </Grid>
                    ))}
                </Grid>
            </Grow>
        )
    }

    return (
        <Grow in>
            <Grid className={classes.container} container alignItems='stretch' spacing={3}>
                {articles.map((article, i) => (
                    <Grid key={i} item xs={12} sm={6} md={4} lg={3} style={{ display: 'flex' }}>
                        <NewsCard article={article} activeArticle={activeArticle} i={i} />
                    </Grid>
                ))}
            </Grid>
        </Grow>
    )
}